.featured {
  height: 100vh;
}
.featured--vertical {
  width: inherit;
  height: inherit;
  background: linear-gradient(to top, #1111 10%, transparent 90%);
}
.featured--horizontal {
  width: inherit;
  height: inherit;
  background: linear-gradient(to right, #1111 30%, transparent);
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding-left: 30px;
  padding-bottom: 150px;
  padding-top: 70px;
}
.featured--name {
  font-size: 60px;
  font-weight: bold;
}

.featured--info {
  font-size: 18px;
  font-weight: bold;
  margin-top: 15px;
}

.featured--points,
.featured--year,
.featured--seasons {
  display: inline-block;
  margin-top: 15px;
}

.featured--points {
  color: aquamarine;
}

.featured--description {
  margin-top: 15px;
  font-size: 20px;
  color: #999;
  max-width: 40%;
}

.featured--buttons {
  margin-top: 15px;
}
.featured--watchbutton,
.featured--mylistbutton {
  display: inline-block;
  font-size: 20px;
  font-weight: bold;
  padding: 12px 25px;
  border-radius: 5px;
  text-decoration: none;
  margin-right: 10px;
  opacity: 1;
  transition: all ease 0.2s;
}
.featured--watchbutton,
.featured--mylistbutton {
  opacity: 0.7;
}
.featured--watchbutton {
  background-color: white;
  color: black;
}

.featured--mylistbutton {
  background-color: gray;
  color: whitesmoke;
}

.featured--genres {
  margin-top: 15px;
  font-size: 18px;
  color: gray;
}
